/**
 * Temporary home for utilities relating to serializing/parsing view objects to/from strings
 * A view object is a minimal group of selection, orientation and zoom which enables a degree of view reproduction, it does not map directly to the concept of a 'view' in jolecule
 */

/**
 * Format a number as a string rounded to a given number of decimal places and optional forced sign
 * @param {number} num
 * @param {number} decimalPlaces
 * @param {boolean} [forceSign]
 * @returns
 */
function formatNum (num, decimalPlaces, forceSign = false) {
  const scale = Math.pow(10, decimalPlaces);
  const m = Number((Math.abs(num) * scale).toPrecision(15));
  const rounded = Math.round(m) / scale * Math.sign(num);
  return `${forceSign && rounded > 0 ? '+' : ''}${rounded}`;
}

/**
 * Compress a given selection into start/end/chain/scheme range list
 * @param {{ scheme: 'uniprot'|'pdb', residues: number[], chain?: string }} selection
 * @returns {{ start: number, end: number, chain: string, scheme: 'uniprot'|'pdb' }[]}
 */
function compressSelection(selection) {
    return [...selection.residues].sort((a, b) => a - b).reduce((acc, val) => { // sort numerically without mutating original array
      if (acc.length === 0 || val - acc[acc.length - 1].end > 1) acc.push({ start: val, end: val, chain: selection.chain, scheme: selection.scheme }); // push new range if none exist or if residue count skips
      else acc[acc.length - 1].end++; // otherwise increment end of existing range
      return acc;
    }, []);
}

/**
 * Parse a comma separated selection string where each entry is of one of the following forms: X-Y | CX | CX-Y | CX-CY,
 * where X and Y are positive integers specifying a single residue or range of residues and C is a chain character,
 * into an array of selection tuples. Selections with no chain are considered uniprot scheme selections
 * @param {string} selectionStr
 * @returns {{ scheme: 'uniprot'|'pdb', residues: number[], chain?: string }[]}
 */
function parseSelection (selectionStr) {
  // parse start/end/chain range list (end chain allowed but not captured, start chain applies to whole range)
  const ranges = Array.from(selectionStr.matchAll(/(?<chain>[A-Z]?)(?<start>\d+)(?:-(?:[A-Z]?)(?<end>\d+))?/gm)).map(({ groups: g }) => ({ chain: g.chain || '', start: Number.parseInt(g.start), end: Number.parseInt(g.end) || Number.parseInt(g.start) }));

  // expand ranges and aggregate into an array of selection tuples
  return ranges.reduce((acc, val) => {
    const residues = val.end == val.start ? [val.start] : [...Array(val.end - val.start + 1).keys()].map((v, k) => k + val.start); // expand
    let selectionTuple = acc.find(({ scheme, chain }) => !val.chain ? scheme === 'uniprot' : (scheme === 'pdb' && chain === val.chain)); // get existing tuple
    if (!selectionTuple) acc.push(selectionTuple = !val.chain ? { scheme: 'uniprot', chain: null, residues: [] } : { scheme: 'pdb', chain: val.chain, residues: [] }); // or create new if none exists
    selectionTuple.residues = [...selectionTuple.residues, ...residues];
    return acc;
  }, []);
}

/**
 * Parse an orientation string of the form (<pitch>,<yaw>,<roll>) where negative signs and decimal places may be in use
 * @param {string} orientationStr
 */
function parseOrientation (orientationStr) {
  const [, pitch, yaw, roll] = orientationStr.match(/\((-?\d*\.?\d*),(-?\d*\.?\d*),(-?\d*\.?\d*)\)/).map(v => Number.parseFloat(v));
  return { pitch, yaw, roll };
}

/**
 * Parse a string of the form <value><units> where signs and decimal places may be in use in the value and units is either '%', 'A', or 'nm'
 * @param {string} orientationStr
 */
function parseZoom (zoomStr) {
  const { rawValue, units } = zoomStr.match(/(?<rawValue>[+-]?\d+\.?\d+)(?<units>%|A|nm)/).groups;
  return { value: Number.parseFloat(rawValue), units}
}

/**
 * Parse view info from an ampersand separated fragment string (with or without beginning hash symbol)
 * @param {string} fragment
 */
export function parseView (fragment) {
  // extract selection, orientation, zoom strings
  const [,selectionStr] = fragment.match(/(?:^|#)?((?:[A-Z]?\d+(?:-[A-Z]?\d+)?,)*[A-Z]?\d+(?:-[A-Z]?\d+)?)(?:$|&)/) || [null, null];
  const [,orientationStr] = fragment.match(/(?:^|#)?(\((?:-?\d*\.?\d*),(?:-?\d*\.?\d*),(?:-?\d*\.?\d*)\))(?:$|&)/) || [null, null];
  const [,zoomStr] = fragment.match(/(?:^|#)?([+-]?\d+\.?\d+(?:%|A|nm))(?:$|&)/) || [null, null];

  return {
    selection: selectionStr ? parseSelection(selectionStr) : {}, // default to no selection
    orientation: orientationStr ? parseOrientation(orientationStr) : { pitch: 0, yaw: 0, roll: 0 }, // default to identity
    zoom: zoomStr ? parseZoom(zoomStr) : { value: 100, units: '%' }, // default to 100%
  };
}

/**
 * Serialize an array of selection tuples into a string
 * @param {{ scheme: 'uniprot'|'pdb', residues: number[], chain?: string }[]} selection
 */
function serializeSelection (selection) {

  // sort such that uniprot scheme selections are first and then pdb selections ordered alphabetically by their chain
  const sortedSelection = [...selection.filter(s => s.scheme === 'uniprot'), ...selection.filter(s => s.scheme === 'pdb').sort()]

  // compress selection residues into ranges and deaggregate into flat array
  const ranges = sortedSelection.reduce((acc, sel) => [...acc,  ...compressSelection(sel)], []);

  // convert to strings (end skipped where start === end, chain not added to end when used) and join with ,
  return ranges.map(({ scheme, chain, start, end }) => `${scheme === 'uniprot' ? '' : chain}${start}${start === end ? '' : `-${end}`}`).join(',');
}

/**
* Serialize a { pitch, yaw, roll } orientation object into a string
* @param {{ pitch: number, yaw: number, roll: number }} orientation
*/
function serializeOrientation ({ pitch, yaw, roll }) {
  return `(${formatNum(pitch, 0)},${formatNum(yaw, 0)},${formatNum(roll, 0)})`; // 0 decimal places
}

/**
* Serialize a { value, units } zoom object into a string
* @param {{ value: number, units: string }} zoom
*/
function serializeZoom ({ value, units }) {
  return `${formatNum(value, 0)}${units}`; // 0 decimal places, no forced sign (for brevity)
}

/**
* Encode view into fragment string
* @param {{ selection: object, orientation: { pitch: number, yaw: number, roll: number }, zoom: { value: number, units: string } }} view
*/
export function serializeView ({ selection, orientation, zoom }) {

  // ignore near-identity orientations
  if (Math.abs(orientation.pitch) < Number.EPSILON && Math.abs(orientation.yaw) < Number.EPSILON && Math.abs(orientation.roll) < Number.EPSILON) orientation = null;

  // ignore near-100% zooms
  if (Math.abs(zoom.value - 100) < Number.EPSILON && zoom.units === '%') zoom = null;

  const components = [
    selection ? serializeSelection(selection) : null,
    orientation ? serializeOrientation(orientation) : null,
    zoom ? serializeZoom(zoom) : null
  ];

  return components.filter(c => c).join('&');
}
