export function displayProtSynonyms (data) {
  var pnames = ''
  var gnames = '<b>Genes:</b>&nbsp;'
  if (data.synonyms == 'none') {
    var preferred_protein_name = 'unknown'
    pnames = ' '
    gnames = ' '
  } else {
    if (preferred_protein_name === 'unknown') {
      preferred_protein_name = data.Synonym
    }
    var gns = []
    if(data.genes.length > 0) {
      gns = data.genes
    }
    else {
      gns[0] = data.Synonym
    }
    var syns = data.synonyms

    if (syns.length) {
      var first = preferred_protein_name
      if (gns && gns.length && first === gns[0]) {
        first = syns[0]
      }
      pnames += "<a href='http://www.uniprot.org/uniprot/" + store.state.structure.accession + "' title='Go to UniProt'>" + first + '</a>'
      for (var n = 0; n < syns.length; ++n) {
        if (syns[n] !== preferred_protein_name) {
          pnames += ', ' + syns[n]
        }
      }
    }

    if (gns && gns.length) {
      if (gns.length > 1) {
        gnames = '<b>Genes:</b>&nbsp;'
      } else {
        gnames = '<b>Gene:</b>&nbsp;'
      }
      gnames += "<a href='http://www.uniprot.org/uniprot/" + store.state.structure.accession + "' title='Go to UniProt'>" + gns[0] + '</a>'
      for (var i = 1; i < gns.length; i++) {
        gnames += ', ' + gns[i]
      }
      data.synonyms.sort(function(a, b){
        // ASC  -> a.length - b.length
        // DESC -> b.length - a.length
        return a.length - b.length
      })
      if(data.synonyms[0].length < gns[0].length && gns[0].length > 6) {
        store.commit('setGene', data.synonyms[0])
      } else {
        store.commit('setGene', gns[0])
      }
      
      gnames += '<br>'
    }

    $('div#psyns').empty()
      .append('<p>' + pnames + '</p>')

    $('div#psyns p').expander({
      slicePoint: 70,
      expandText: '[+]',
      userCollapseText: '[&ndash;]',
      afterExpand: function () { // console.log("afterExpand: aligning "+$(this));
        $(this).children('.details').attr('style', 'display: inline;')
      }
    })

    // add para after form fields
    $('div#gsyns').show()
      .empty()
      .append('<p>' + gnames + '</p>')

    $('div#gsyns p').expander({
      slicePoint: 70,
      expandText: '[+]',
      userCollapseText: '[&ndash;]',
      afterExpand: function () { // console.log("afterExpand: aligning "+$(this));
        $(this).children('.details').attr('style', 'display: inline;')
      }
    })

    // NEBLINA's SCRIPT FOR AFFORDANCE VIEW
    if (gns && gns.length) {
      var gene_name = gns[0]
    }

    $('#gene_name').show()
      .empty()
      .append(gene_name)

    var geneLeft = $('#affordance_mode').width() / 2 - $('#gene_name').width() / 2
    geneLeft = geneLeft + 'px'
    $('#gene_name').css({
      'margin-left': geneLeft
    })

    // update ABOUT title
    $('#about h3 span.explanation').html("<a href='http://www.uniprot.org/uniprot/" + store.state.structure.accession + "' title='Go to UniProt'>" + store.state.Gene + '</a>')
  }

  // clear form field if not empty
  if ($('#protein_syn_input')[0].value != '') { $('#protein_syn_input')[0].value = '' }
};

