export function update3DTitle (accession, pdbId, chainId, moleculeName, score) {
  if (chainId && moleculeName) {
    var shortName = moleculeName
    if (shortName.indexOf('(') !== -1) {
      shortName = moleculeName.substring(0, moleculeName.indexOf('(')).trim()
    }

    if (accession && pdbId && score) {
      var currentMember = store.state.Matches.clusters[store.state.pdbIndex.cluster].members[store.state.pdbIndex.member]
      var evalue = currentMember.E_value // e-value from pssh2
      explainTitle(store.state.Gene, shortName, pdbId, chainId, score, evalue)
    } else { // DNA or RNA (no accession)
      // _this.primary_accession = shortName
      store.commit('setGene', shortName)
    }
    // @TODO Find a better place to put the following code to update URL
    var urlParams = window.location.href.substr(window.location.origin.length + window.location.pathname.length)
    history.pushState('', document.title, '/' +
    store.state.Organism + '/' + store.state.Gene + '/' + store.state.structure.pdb + '/' + store.state.structure.chain + urlParams)
  } else {
    document.querySelector('#uniProtDesc').text(store.state.Gene)
  }
}

export function explainTitle (uniprotName, pdbName, pdbId, chainId, score, evalue) {
  if (store.getters.getPdbData) {
    var pdbData = store.getters.getPdbData
  } else {
    pdbData = store.state.pdbChainData
  }
  // store.commit('setPdbChainData', {})
  pdbData.pdbName = pdbName
  pdbData.score = score
  pdbData.uniprotName = uniprotName
  var Log10E
  Log10E = -Math.log(evalue) / Math.LN10
  var precision = (BioScience_PlantDisease_Weibull_model(Log10E) * 100)
  precision = Math.round(precision)
  pdbData.evalueString = evalue.toString().split('e')[0]
  pdbData.power = evalue.toString().split('e')[1]
  if (evalue === 0 && precision === 1) {
    pdbData.precisiontxt = 'close to 100'
  } else {
    pdbData.precisiontxt = ' ' + precision
  }
  if (evalue > 10E-72) {
    pdbData.quality = 'in the twilight zone'
    pdbData.qualClass = 'twilight'
  } else {
    pdbData.quality = 'high quality'
    pdbData.qualClass = 'high'
  }
  store.commit('setPdbChainData', '')
  store.commit('setPdbChainData', pdbData)
}

