import * as panel3d from '../components/StructureViewer/helpers/joleculeAPI'
import * as viewUtils from '../components/InfoAbout/helpers/ViewUtils'
var alignment, controller, soup

export function onkeydown (event) {
  alignment = panel3d.joleculeAlignment
  controller = panel3d.embededJolecule.controller
  soup = panel3d.embededJolecule.soup
  const c = String.fromCharCode(event.keyCode).toUpperCase()
  if (event.keyCode === 37) {
    const isClear = !event.shiftKey
    controller.selectPrevResidue(isClear)
  } else if (event.keyCode === 39) {
    const isClear = !event.shiftKey
    controller.selectNextResidue(isClear)
  } else if (event.keyCode === 13) {
    const residue = soup.getResidueProxy()
    const selection = [...Array(soup.getResidueCount()).keys()].filter((k, v) => residue.load(k).selected)
    if (selection.length > 0) {
      controller.zoomToSelection() // zoom to selection
    } else if (soup.selectedTraces.length > 0) {
      const { iAtom } = residue.load(soup.traces[soup.selectedTraces[0]].indices[0])
      controller.zoomToChainContainingAtom(iAtom) // zoom to chain
    } else {
      controller.zoomOut() // zoom to structure
    }
  } else if (event.keyCode === 27) {
    alignment.selectSeq = ''
    controller.clearSelectedResidues()
    viewUtils.saveToUrl()
  } else if (c === 'C' && event.metaKey) {
    alignment.copyToClipboard()
  } else {
    if (document.activeElement === document.body) {
      if (c.match(/^[A-Z]+$/i)) {
        alignment.selectNextChar(c)
      }
    }
  }
}

document.addEventListener('paste', e => {
  if (document.activeElement !== document.body) {
    return
  }
  const data = e.clipboardData
  if (data && data.getData) {
    alignment.selectSeq = ''
    const text = data.getData('text/plain')
    // console.log('AlignAquaria.paste', text)
    for (const c of text.toUpperCase()) {
      alignment.selectNextChar(c)
    }
  }
})
