import $ from 'jquery'

export function checkPhone () {
  var iDevices = [
    'iPad Simulator',
    'iPhone Simulator',
    'iPod Simulator',
    'iPad',
    'iPhone',
    'iPod'
  ]

  if (navigator.platform) {
    while (iDevices.length) {
      if (navigator.platform === iDevices.pop() || (screen.width >= 300 && screen.width <= 600)) { return true }
    }
  } else {
    return false
  }
}

export function appendPopup (text, position, width, featureMap) {
  $('div#popup').removeAttr('style')
  var s, tailLeft, bottom
  $('div#popup').removeClass('fade')

  var loadedPin = document.getElementById('loadedPin')
  if ((document.getElementById('Features') && document.getElementById('Features').classList.contains('active'))){
    if (loadedPin.classList.contains('loadedPin_active')) {
      loadedPin.classList.remove('loadedPin_active')
    }
    if (!loadedPin.classList.contains('loadedPin')) {
      loadedPin.classList.add('loadedPin')
    }
  }
  // document.querySelector('#popuptext').innerHTML = text
  $('div#popup').fadeIn(60)
  
  // var popheight = document.querySelector('div#popup').offsetHeight

  var bleft = parseInt(position.left + (width / 2) - 120)
  // var btop = parseInt(position.top - popheight)

  // var tailLeft = parseInt(position.left + (width / 2))
  console.log("in the common.js functions")
  document.querySelector('#popupTail').style.marginLeft = '107px'
  // document.querySelector('#popupTail').style.top = btop + 'px'
  if (featureMap) {
    bottom = '17px'
  } else {
    bottom = parseInt(window.innerHeight - position.bottom) + 'px'
  }

  $('div#popup').css({
    left: bleft + 'px',
    // top: btop + 'px',
    bottom: bottom,
    // width: '430px'
  }).fadeIn(600)

  if (bleft < 0) {
    tailLeft = 107 + bleft + 3
    document.querySelector('#popupTail').style.marginLeft = tailLeft + 'px'
    document.querySelector('div#popup').style.left = '-5px'
  }
  if (bleft + document.querySelector('div#popup').offsetWidth + 10 > document.querySelector('#structure-viewer').clientWidth) {
    let popupWidth = document.getElementById('popup').offsetWidth
    document.querySelector('div#popup').style.left = (document.querySelector('#structure-viewer').clientWidth + 11 - popupWidth) + 'px'
    tailLeft = 107 + (popupWidth - (document.querySelector('#structure-viewer').clientWidth - bleft) - 11) + 15
    document.querySelector('#popupTail').style.marginLeft = tailLeft + 'px'
  }

  document.querySelector('#popupTail').classList.remove('popupTail_noShow')
  document.querySelector('#popupTail').classList.add('popupTail_')
  // Click on X to close popup
  /* $('span.x').on('click', function () {
    $('div#popup').fadeOut()
  })
  */

  $('div#popup').on('hover', function () {
    clearTimeout(s)
  }, function () {
    s = setTimeout(function () {
      $('div#popup').fadeOut()
    }, 500)
  })
}
