/**
 * Temporary home for utilities relating to reading/setting the view and associated URL operations
 */

import { Matrix4, Euler, MathUtils, Vector3 } from 'three'
import * as panel3d from '../../StructureViewer/helpers/joleculeAPI'
import { parseView, serializeView } from './ViewEncodingUtils.js';
import * as store from '../../../store/index'

const NANOMETER_TO_WORLD = 10;
const WORLD_TO_NANOMETER = 1 / NANOMETER_TO_WORLD;
const ANGSTROM_TO_WORLD = 1;
const WORLD_TO_ANGSTROM = 1 / ANGSTROM_TO_WORLD;

// proxy object for global access to jolecule with error checking and type injection
const jolecule = {

  __check () {
    if (!panel3d || !panel3d.embededJolecule) throw new Error('Tried to access Jolecule widget before it was available');
  },

  /**
   * Grab soup view
   * @returns {import('jolecule/src/soup-view').SoupView}
   */
  get view () { this.__check(); return panel3d.embededJolecule.soupView },

  /**
   * Grab soup data from Jolecule
   * @TODO remove global reference
   * @returns {import('jolecule/src/soup').Soup}
   */
  get soup () { this.__check(); return panel3d.embededJolecule.soup },

  /**
   * Grab view controller from Jolecule
   * @TODO remove global reference
   * @returns {import('jolecule/src/soup-view').SoupViewController}
   */
  get viewController () { this.__check(); return panel3d.embededJolecule.controller },

  /**
   * Grab view controller from Jolecule
   * @TODO remove global reference
   * @returns {import('jolecule/src/soup-widget').SoupWidget}
   */
  get soupWidget () { this.__check(); return panel3d.embededJolecule.soupWidget },

  /**
   * Grab the current aquaria alignment
   * @TODO remove global reference
   * @returns {import('jolecule/src/aquaria').AquariaAlignment}
   */
  get alignment () { this.__check(); return panel3d.joleculeAlignment },
}

/**
 * Get a list of currently selected residue indices (iRes)
 * @returns {number[]}
 */
function getSelectedResidues () {

  // similar functionality to Jolecule's soup.makeSelectedResidueList but that doesn't quite do what you'd expect, bug?
  const residue = jolecule.soup.getResidueProxy();
  return [...Array(jolecule.soup.getResidueCount()).keys()].reduce((acc, iRes) => {
    if (residue.load(iRes).selected) acc.push(iRes);
    return acc;
  }, []);
}

/**
 * Get the active chain from Jolecule state
 * See Jolecule soup.isSameChainSelected
 */
function getSelectedChain () {
  const soup = jolecule.soup;
  const traces = soup.selectedTraces.map(i => jolecule.soup.traces[i]);
  const chains = traces.map(t => soup.getResidueProxy(t.indices[0]).chain);
  return chains[0];
}

/**
 * Get focus point corresponding to the current context - selected residues, selected chain, or whole structure
 */
function getContextualFocus () {

  const soup = jolecule.soup;
  const view = jolecule.view;
  const selectedResidues = getSelectedResidues();

  let atoms = [];

  // selected residue atoms
  if (selectedResidues.length > 0) {
    const residue = soup.getResidueProxy();
    atoms = selectedResidues.map(iRes => residue.load(iRes).iAtom);
  }

  // active chain atoms
  else if (view.currentView.selectedTraces.length > 0) {
    const residue = soup.traces[view.currentView.selectedTraces[0]].indices[0];
    atoms = soup.getAtomsOfChainContainingResidue(residue);
  }

  // all atoms
  else {
    atoms = [...Array(soup.getAtomCount()).keys()];
  }

  return soup.getCenter(atoms);
}

/**
 * Get 100% zoom for the current structure
 */
function getDefaultZoom () {
  const maxLength = jolecule.soup.calcMaxLength();
  return Math.abs(maxLength * 1.75); // matching Jolecule approach (see soup-widget.js getZoomedOutViewOf)
}

/**
 * Get a Jolecule view object from Aquaria reproducible view data (orientation and zoom)
 * with focus based on the current context (selection and/or chain)
 */
function getAquariaView (orientation, zoom) {

  // @TODO cache these results somewhere and only update once relevant Jolecule state changes
  const defaultZoom = getDefaultZoom();
  const focus = getContextualFocus();

  // clone the current view which has a bunch of state we need to keep
  const view = jolecule.view.currentView.clone();

  let zoomWorldUnits = defaultZoom; // world units
  switch (zoom.units) {
    case 'nm':
      zoomWorldUnits = zoom.value *= NANOMETER_TO_WORLD;
      break;
    case 'A':
      zoomWorldUnits = zoom.value *= ANGSTROM_TO_WORLD;
      break;
    case '%':
      zoomWorldUnits /= zoom.value / 100;
      break;
    default:
      console.warn(`Unrecognised zoom units: ${zoom.units}`);
      zoomWorldUnits = zoom.value;
      break;
  }

  // create new camera params
  const cameraParams = {
    zFront: -defaultZoom / 2, // little longer than Jolecule default which is half of maxLength from which default zoom is derieved
    zBack: defaultZoom / 2, // as above
    zoom: zoomWorldUnits,
    focus,
  };

  // yaw shifted so that (0,0,0) matches the Jolecule 'default's
  const rotation = new Euler(MathUtils.degToRad(orientation.pitch), MathUtils.degToRad(orientation.yaw + 180), MathUtils.degToRad(orientation.roll), 'YXZ');

  cameraParams.up = new Vector3(0, 1, 0).applyEuler(rotation);
  cameraParams.position = new Vector3().copy(cameraParams.focus).add(new Vector3(0, 0, cameraParams.zoom).applyEuler(rotation));

  view.setCamera(cameraParams);
  view.iAtom = jolecule.soup.getIAtomAtPosition(focus);

  return view;
}

/**
 * Gets selected residues as an array of (scheme, resNum[], chain) tuples.
 * For the "uniprot" scheme, residue numbers represent UniProt sequence numbering and chain is null
 * For the "pdb" scheme, residue numbers represent PDB structure numbering
 * @returns {{ scheme: 'uniprot'|'pdb', residues: number[], chain?: string }[]}
 */
function getCurrentSelection () {
  const selection = [];
  const residue = jolecule.soup.getResidueProxy()
  for (const iRes of getSelectedResidues()) {
    residue.load(iRes);
    const useUniProtNumbering = jolecule.soup.isSameChainSelected(iRes);
    let selectionTuple = selection.find(({ scheme, chain }) => useUniProtNumbering ? scheme === 'uniprot' : (scheme === 'pdb' && chain === residue.chain)); // get existing tuple
    if (!selectionTuple) selection.push(selectionTuple = useUniProtNumbering ? { scheme: 'uniprot', chain: null, residues: [] } : { scheme: 'pdb', chain: residue.chain, residues: [] }); // or create new if none exists
    // for the active chain, get the sequence residue number rather than the pdb number
    const residueNumber = useUniProtNumbering ? jolecule.alignment.mapPdbResOfChainToSeqRes(jolecule.soup.structureIds[0], residue.chain, residue.resNum)[1] : residue.resNum;
    selectionTuple.residues.push(residueNumber);
  }
  return selection;
}

function getCurrentOrientation () {
  const currentView = jolecule.view.currentView;
  const forward = currentView.cameraParams.position.clone().sub(currentView.cameraParams.focus).normalize()
  const up = currentView.cameraParams.up
  const side = up.clone().cross(forward)
  const up2 = forward.clone().cross(side)
  const rot = new Matrix4()
  rot.set(side.x, up2.x, forward.x, 0,
    side.y, up2.y, forward.y, 0,
    side.z, up2.z, forward.z, 0,
    0, 0, 0, 1)
  const [pitch, yaw, roll] = new Euler().setFromRotationMatrix(rot, 'YXZ').toArray().map(v => MathUtils.radToDeg(v));
  return { pitch, yaw: yaw - 180, roll }
}

function getCurrentZoom () {

  const defaultZoom = getDefaultZoom(); // @TODO cache this result somewhere and only update once relevant Jolecule state changes
  const currentZoom = jolecule.view.currentView.cameraParams.zoom;
  // @TODO app state controls which units used to serialize zoom
  return { value: defaultZoom / currentZoom * 100, units: '%' };
  return { value: currentZoom * WORLD_TO_ANGSTROM, units: 'A' };
  return { value: currentZoom * WORLD_TO_NANOMETER, units: 'nm' };
}

export function getCurrentView () {
  return {
    selection: getCurrentSelection(),
    orientation: getCurrentOrientation(),
    zoom: getCurrentZoom()
  }
}

/**
 * Apply selection (array of selection tuples, each with a list of residues and a scheme/chain)
 * @param {{ scheme: 'uniprot'|'pdb', residues: number[], chain?: string }[]} [selection]
 */
export function applySelection (selection) {
  const alignment = jolecule.alignment;
  const soup = jolecule.soup;
  const soupController = jolecule.viewController;

  // apply selection (if any, if none, current selection will be retained)
  if (Object.keys(selection).length > 0) {
    soupController.clearSelectedResidues();
    for (const { scheme, chain, residues } of selection) {
      if (!chain) {
        chain = store.default.state.structure.chain
      }
      const relevantChain = scheme === 'uniprot' ? getSelectedChain() : chain; // need to find the relevant chain if uniprot scheme
      const residuesToSelect = scheme === 'uniprot'
        ? residues.map(r => alignment.mapSeqResToPdbResOfChain(soup.structureIds[0], r, getSelectedChain())[2]) // if scheme is uniprot, map the uniprot numbering to pdb numbering using relevant chain
        : residues; // if scheme is pdb, keep numbering as-is

      // select the residues
      for (const residueToSelect of residuesToSelect) {
        soupController.setResidueSelect(soup.findFirstResidue(relevantChain, residueToSelect).iRes, true); // use this instead of selectResidue so it doesn't appear like a click
      }
    }
    store.default.commit('setResidueSelected', true)
  } else {
    store.default.commit('setResidueSelected', false)
  }
}

/**
 * Update URL with the current view information
 */
export function saveToUrl () {
  const view = serializeView(getCurrentView());
  let fragment = view
  const myRegexp = /[a-z A-Z]{3,}/;
  const hash = window.location.hash
  if (view && myRegexp.exec(hash)) {
    fragment = view + '&' + myRegexp.exec(hash)[0]
  }
  // @TODO set through router instead
  var cleaned = decodeURIComponent(window.location.search)
  if (view) {
    history.replaceState(null, null, `${window.location.origin}${window.location.pathname}${cleaned}#${fragment}`);
  } else {
    loadFromUrl() // load the current view (orientation etc) from URL
  }
}

/**
 * Load and apply view information from the current URL
 */
export function loadFromUrl () {

  // @TODO access through some application state instead (parse at router level and store in a watchable manner)
  const fragment = location.hash;

  const { selection, orientation, zoom } = parseView(fragment);
  // store list of selected residues
  if (Object.keys(selection).length > 0) {
    store.default.commit('setSelectedResidues', selection[0].residues)
  }
  // apply selection
  applySelection(selection);

  // get an Aquaria view using specified orientation and zoom (accounts for the focus point resulting from the selection set above)
  const view = getAquariaView(orientation, zoom);

  // apply the Aquaria view (immediate)
  jolecule.view.setCurrentView(view);
}

/**
 * Load and apply view information from the current URL, excluding selection (aka apply orientation/zoom to current focus point)
 */
 export function reapplyFromUrl () {

  // @TODO access through some application state instead (parse at router level and store in a watchable manner)
  const fragment = location.hash;

  const { orientation, zoom } = parseView(fragment);

  // get an Aquaria view using specified orientation and zoom (accounts for the CURRENT focus point)
  const view = getAquariaView(orientation, zoom);

  // apply the Aquaria view (animated)
  jolecule.view.setTargetView(view);
}

/**
 * Grab the current raw camera pose with optional handedness conversion
 * @param {Boolean} convertToLHS
 * @returns {{ position: Number[], orientation: Number[] }}
 */
export function getRawCameraPose (convertToLHS = true) {
  const position = jolecule.soupWidget.camera.position.clone()
  const orientation = jolecule.soupWidget.camera.quaternion.clone()
  if (convertToLHS) {
    position.x = -position.x
    orientation.set(orientation.z, orientation.w, orientation.x, orientation.y)
  }
  return {
    position: [position.x, position.y, position.z],
    orientation: [orientation.x, orientation.y, orientation.z, orientation.w]
  }
}

export class JoleculeWatcher extends EventTarget {

  /**
   * @param {import('jolecule/src/embed-widget').EmbedJolecule} jolecule
   */
  constructor(jolecule) {
    super();
    this.jolecule = jolecule;

    // view state
    this.previousCamera = null;

    // animation state
    this.previousTarget = null;

    // selection state
    this.previousSelected = null;
    this.previousSelection = [];
    this.activeSelectionTimeout = false;

    // watch for view changes as a result of other events
    this._watchForViewCommit();

    this._update();
  }

  _update() {
    requestAnimationFrame(() => this._update());
    this._checkView();
    this._checkAnimation();
  }

  _compareCameras(a, b) {
    if (!a && b) return false;
    if (!b && a) return false;
    if (a.zoom !== b.zoom) return false;
    if (!a.position.equals(b.position)) return false;
    if (!a.focus.equals(b.focus)) return false;
    if (!a.up.equals(b.up)) return false;
    return true;
  }

  _watchForViewCommit() {

    (() => { // mouse triggered changes
      let camera = null;
      window.addEventListener('mousedown', () => {
        camera = this._cloneCamera(this.jolecule.soupView.currentView.cameraParams);
      });
      window.addEventListener('mouseup', () => {
        if (!this._compareCameras(camera, this.jolecule.soupView.currentView.cameraParams)) onChange();
      });
    })();

    (() => { // animation triggered changes (Jolecule setTargetView)
      let camera = null;
      this.addEventListener('animationstart', () => {
        camera = this._cloneCamera(this.jolecule.soupView.currentView.cameraParams);
      });
      this.addEventListener('animationend', () => {
        if (!this._compareCameras(camera, this.jolecule.soupView.currentView.cameraParams)) onChange();
      });
    })();

    (() => { // gesture triggered changes
      window.addEventListener('gesturestart', () => {
        if (!this._compareCameras(camera, this.jolecule.soupView.currentView.cameraParams)) onChange();
      });
      window.addEventListener('gesturestart', () => {
        if (!this._compareCameras(camera, this.jolecule.soupView.currentView.cameraParams)) onChange();
      });
    })();

    // wheel assumed to always commit a view change
    window.addEventListener('wheel', () => onChange())

    const onChange = () => {
      this.dispatchEvent(new CustomEvent('viewcommit', { detail: this.jolecule.soupView.currentView.cameraParams }));
    }
  }

  _cloneCamera({ zoom, position, focus, up, zFront, zBack }) {
    return {
      zoom,
      zFront,
      zBack,
      position: position.clone(),
      focus: focus.clone(),
      up: up.clone(),
    }
  }

  _checkView() {
    try {
      const camera = this.jolecule.soupView.currentView.cameraParams;
      if (!this._compareCameras(camera, this.previousCamera)) {
        this.previousCamera = this._cloneCamera(camera);
        this.dispatchEvent(new CustomEvent('viewchange', { detail: camera }))
      }
    } catch {}
  }

  _checkAnimation() {
    const target = this.jolecule.soupView.targetView;
    if (target !== this.previousTarget) {
      this.previousTarget = target;
      if (target === null) this.dispatchEvent(new CustomEvent('animationend'))
      else this.dispatchEvent(new CustomEvent('animationstart'))
    }
  }
}
