import store from '../store/index'
import * as fetchFeature from '../components/Features/helpers/fetch_features'

export async function checkQuery (query, params) {
  var lcquery = []
  var blocks = []
  Object.keys(query).forEach(function (q) {
    lcquery.push(q)
  })
  var featureRegex = new RegExp(/(p\.)?[A-Za-z]+[0-9]+[A-Za-z\*\_\?\[\]\(\)\%\=\.]+/) // eslint-disable-line
  // Check if lcquery is a variant
  /**
   * @todo
   * 1. Check for variants with &
   * 2. Allow file = 'link'
   */
  if (featureRegex.test(lcquery[0]) || lcquery[0].includes('zenodo') || lcquery[0].includes('.json')) {
    var variantResidues = await fetchFeature.ifUrlHasVarExtractInfo()
    store.commit('setVariantResidues', variantResidues)
  }
  lcquery.forEach(function (block) {
    if (block.toLowerCase().includes('sidechain')) {
      blocks.push(block)
    }
  })
  store.commit('setSidechain', blocks)
  // For each query check if matches regex
  // If matches variant : Store varRes. Store current feature. Then call feature on structure load.
  // If matches pre-defined features : store current feature. Then call feature on structure load.
  // If matches zenodo file or json file : store current feature. Then call feature on structure load.
  return null
}
