import Vue from 'vue'
import VueRouter from 'vue-router'
import Matrix from '../views/Matrix.vue'
import Covid from '../views/Covid.vue'
import GdprConsentModal from '../views/GdprConsent.vue'
import Tagger from '../views/Tagger.vue'
import axios from 'axios'
import store from '../store/index'
// import * as LoadAQUARIA from '../utils/loadData'
import * as urlQuery from './urlQuery'

window.BACKEND = process.env.VUE_APP_AQUARIA_BACKEND
Vue.use(VueRouter)

const routes = [
  {
    path: '/consent',
    component: GdprConsentModal
  },
  {
    path: '/omicron',
    component: Covid
  },
  {
    path: '/covid19',
    name: 'Matrix',
    component: Matrix
  },
  {
    path: '/SARS-CoV-2',
    alias: '/covid19',
    component: Matrix
  },
  {
    path: '/:id(\\d+)', // change to organismID
    alias: '/covid19',
    component: Matrix
  },
  {
    path: '/trials',
    name: 'Tagger',
    component: Tagger
  },
  {
    path: '/coverage/:organism?',
    name: 'Coverage',
    component: () => import(/* webpackChunkName: "wrapper" */ '../views/Coverage.vue') // lazy load
  },
  {
    //  Check if path matches UniProt Accession / PDB ID (optional) / Chain (optional)
    path: '/:uniprotAccession([A-Z][0-9][A-Z0-9][A-Z0-9][A-Z0-9][0-9])/:pdbID([0-9][A-Za-z0-9][A-za-z0-9][A-Za-z0-9])?/:chain([a-zA-Z0-9])?', // eslint-disable-line
    name: 'Wrapper',
    props: (route) => ({
      urlRequest: route.params
    }),
    beforeEnter: (to, from, next) => {
      if (to.params.pdbID) {
        store.commit('setUserInput', true)
      }
      if (to.hash.includes('try')) {
        store.commit('setNonUserInput', true)
        window.location.hash = ''
      }
      store.commit('setStructure', { accession: to.params.uniprotAccession, pdb: to.params.pdbID, chain: to.params.chain })
      if (Object.keys(to.query).length > 0) {
        urlQuery.checkQuery(to.query, to.params)
      }
      store.commit('setStructure', { accession: to.params.uniprotAccession, pdb: to.params.pdbID, chain: to.params.chain })
      next()
    },
    component: () => import(/* webpackChunkName: "wrapper" */ '../views/Wrapper.vue') // lazy load
  },
  {
    //  Check if path matches UniProt PDB ID / Chain
    path: '/:pdbID([0-9][A-Za-z0-9][A-za-z0-9][A-Za-z0-9])/:chain([a-zA-Z0-9])', // eslint-disable-line
    name: 'Wrapper',
    props: (route) => ({
      urlRequest: route.params
    }), // Use navigation guards
    beforeEnter: (to, from, next) => {
      if (to.params.pdbID) {
        store.commit('setUserInput', true)
      }
      if (to.hash.includes('try')) {
        store.commit('setNonUserInput', true)
        window.location.hash = ''
      }
      var params = to.params
      var url = `${window.BACKEND}/getAccessionForPDB/${params.pdbID}/${params.chain}`
      axios({
        method: 'get',
        url: url
      }).then(function (response) {
        try {
          if (typeof (response.data) !== 'object') {
            throw response.data
          }
          params.uniprotAccession = response.data.Accession
          params.chain = response.data.Chain
          store.commit('setStructure', { accession: params.uniprotAccession, pdb: params.pdbID, chain: params.chain })
          if (Object.keys(to.query).length > 0) {
            urlQuery.checkQuery(to.query, to.params)
          }
          next()
        } catch (error) {
          console.log(error)
          store.commit('showLoader', true)
          store.commit('setErrorTitle', 'Cannot load PDB.')
          store.commit('setErrorMsg', error + "<br><br> You can view this structure on the PDB website. \n Press 'Report Issue' to tell the Aquaria team, or press 'OK' to return to previous structure.")
          next()
        }
      })
    },
    component: () => import(/* webpackChunkName: "wrapper" */ '../views/Wrapper.vue') // lazy load
  },
  {
    //  Check if path matches UniProt PDB ID
    path: '/:pdbID([0-9][A-Za-z0-9][A-za-z0-9][A-Za-z0-9])', // eslint-disable-line
    name: 'Wrapper',
    props: (route) => ({
      urlRequest: route.params
    }), // Use navigation guards
    beforeEnter: (to, from, next) => {
      if (to.params.pdbID) {
        store.commit('setUserInput', true)
      }
      if (to.hash.includes('try')) {
        store.commit('setNonUserInput', true)
        window.location.hash = ''
      }
      next()
    },
    component: () => import(/* webpackChunkName: "wrapper" */ '../views/Wrapper.vue') // lazy load
  },
  {
    //  Check if path matches Organism Name / Gene / PDB ID (optional) / Chain (optional)
    path: '/:organismName/:gene/:pdbID([0-9][A-Za-z0-9][A-za-z0-9][A-Za-z0-9])?/:chain([a-zA-Z0-9])?', // eslint-disable-line
    name: 'Wrapper',
    props: (route) => ({
      urlRequest: route.params
    }),
    beforeEnter: (to, from, next) => {
      if (to.params.pdbID) {
        store.commit('setUserInput', true)
      }
      if (to.hash.includes('try')) {
        store.commit('setNonUserInput', true)
        window.location.hash = ''
      }
      var params = to.params
      var organismName = params.organismName
      /**
       * If organism name contains a '/' or '|', use the pre-special characters string
       */
      if (organismName.includes('|')) {
        organismName = organismName.split('|')[0]
      }
      if (organismName.includes('/')) {
        organismName = organismName.split('/')[0]
      }
      var url = `${window.BACKEND}/getAccessionbyOrgNameandGene/${organismName.replaceAll('_', ' ')}/${params.gene}`
      try {
        axios({
          method: 'get',
          url: url
        }).then(function (response) {
          /**
           *  If organism name contains any space, store name
           * by replacing space with '_' for cleaner URL
           *
           *  If organism name contains /, store name
           * by replacing '/' using '|', so that it does not cause errors
           * */
          // console.log(response.data[0].sequence)
          store.commit('setInitialSequence', response.data[0].Sequence)

          organismName = params.organismName.replaceAll(' ', '_')
          organismName = organismName.replaceAll('/', '|')
          store.commit('setOrganism', organismName)
          store.commit('setGene', params.gene)

          params.uniprotAccession = response.data[0].Primary_Accession
          store.commit('setStructure', { accession: params.uniprotAccession, pdb: params.pdbID, chain: params.chain })
          if (Object.keys(to.query).length > 0) {
            urlQuery.checkQuery(to.query, params)
          }
          next()
        })
      } catch (error) {
        console.log(error)
        store.commit('showLoader', true)
        store.commit('setErrorTitle', 'Cannot find PDB.')
      }
    },
    component: () => import(/* webpackChunkName: "wrapper" */ '../views/Wrapper.vue') // lazy load
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
