export async function loadAccession (primaryAccession, pdb, autoSelectChain) {
  try {
    const query = store.state.queryStore
    if (location.search) {
      query[primaryAccession] = location.search
      store.commit('setQueryStore', query)
    }
    store.commit('setStructureAlignment', null)
    var chainParam = autoSelectChain ? '/' + autoSelectChain : ''
    var pdbParam = pdb ? (pdb.includes('[') ? '/' + pdb.split('[')[0] : '/' + pdb) : ''
    if (pdb !== '') {
      store.commit('setInitPdb', pdb)
    }
    var urlParams = window.location.href.substr(window.location.origin.length + window.location.pathname.length)
    console.log('"Decoding URL ... " ' + urlParams)
    if (!primaryAccession || primaryAccession === 'undefined') {
      /**
       * @todo
       * here we want to revert to the initial state, e.g. no structure in the viewer
       */
      store.commit('showLoader', true)
      store.commit('setLoaderMsg', 'Fetching matching structures')
      return null
    }
    /**
     * @todo
     * Move code to route.js
     */
    if (store.getters.getOrganism) {
      history.pushState(primaryAccession, document.title, '/' +
      store.getters.getOrganism + '/' + store.getters.getGene + pdbParam + chainParam + urlParams)
    } else {
      history.pushState(primaryAccession, document.title, '/' +
        primaryAccession + pdbParam + (chainParam || '') + urlParams)
    }

    var loadRequest = {
      selector: primaryAccession,
      selectPDB: pdb, // optional fields
      selectChain: autoSelectChain // optional fields
    }
    var matches = await getMatchingStructures(loadRequest)
    // var variantRes = await fetch_features.ifUrlHasVarExtractInfo() // eslint-disable-line no-unused-vars
    // console.log(variantRes)
    if (Object.keys(store.getters.getVariantResidues).length > 0) { // need to check feature from url
      console.log('This should now not run...')
      var data = await getTheBestStructureForVar(pdbParam, chainParam, loadRequest.selector[0], store.getters.getVariantResidues)
      matches.Selected_PDB = data.Selected_PDB ? data.Selected_PDB : data.Best_PDB
      console.log('Genious')
      console.log(data)
      if (data.variantStructs) {
        store.commit('setvariantStructs', data)
      }
      if (data.intersectingStructs) {
        store.commit('setIntersectingStructs', data.intersectingStructs)
      }
      store.commit('setStructure', { accession: primaryAccession[0], pdb: matches.Selected_PDB.pdb_id, chain: matches.Selected_PDB.pdb_chain[0] })
      // Store.commit('loadRequest', loadRequest)
      processMatches(matches)
    } else if (location.search.includes('zenodo') || location.search.includes('.json')) {
      var variantResidues = await fetchFeature.ifUrlHasVarExtractInfo()
      store.commit('setVariantResidues', variantResidues)
      data = await getTheBestStructureForVar(pdbParam, chainParam, loadRequest.selector[0], store.getters.getVariantResidues)
      matches.Selected_PDB = data.Selected_PDB ? data.Selected_PDB : data.Best_PDB
      console.log('Genious')
      console.log(data)
      if (data.variantStructs) {
        store.commit('setvariantStructs', data)
      }
      if (data.intersectingStructs) {
        store.commit('setIntersectingStructs', data.intersectingStructs)
      }
      store.commit('setStructure', { accession: primaryAccession[0], pdb: matches.Selected_PDB.pdb_id, chain: matches.Selected_PDB.pdb_chain[0] })
      // Store.commit('loadRequest', loadRequest)
      processMatches(matches)
    } else {
      if (autoSelectChain) {
        matches.Selected_PDB.pdb_chain = [autoSelectChain]
      }
      store.commit('setStructure', { accession: primaryAccession[0], pdb: matches.Selected_PDB.pdb_id, chain: matches.Selected_PDB.pdb_chain[0] })
      // Store.commit('loadRequest', loadRequest)
      processMatches(matches)
    }
    // else if (window.location.search.includes('zenodo')) {
    //   handleZenodo.getDataUrl(window.location.search.split('?')[1])
    //   Store.commit('setStructure', { accession: primaryAccession[0], pdb: matches.Selected_PDB.pdb_id, chain: matches.Selected_PDB.pdb_chain[0] })
    //   // Store.commit('loadRequest', loadRequest)
    //   processMatches(matches, skip3DView)
    // }
  } catch (err) {
    console.log(err)
  }
}

function processMatches (matches) {
  if (!matches.err) {
    displayMember()
  }
}

function totalStructCount (matches) {
  const err = matches.err
  var total = 0
  if (err !== undefined && err !== null) {
    store.commit('setErrorTitle', err.message)
  } else {
    matches.clusters.forEach(function (cluster) {
      total += parseInt(cluster.cluster_size)
    })
    // @TODO Replace by vue (x.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ","))
    store.commit('setStructCount', total)
  }
}

async function getMatchingStructures (loadRequest) {
  var matches
  var url = `${window.BACKEND}/get_matching_structures`
  try {
    const response = await axios({
      method: 'get',
      url: url,
      params: loadRequest
    })
    if (typeof (response.data) === 'string' || response.data.err) {
      throw (response.data)
    } else {
      matches = response.data
      store.commit('setLoaderMsg', 'Initialising viewer')
      store.commit('setMatches', matches)
      updateSequence(matches.sequences)
      totalStructCount(matches)
      return matches
    }
  } catch (err) {
    store.commit('setErrorTitle', 'Matching Structures Error')
    var errMsg = err
    if (loadRequest.selectPDB) {
      errMsg = errMsg + 'You can view this structure on the PDB website.'
    }
    errMsg = errMsg + "<br><br> \n Press 'Report Issue' to tell the Aquaria team, or press 'OK' to return to previous structure."
    store.commit('setErrorMsg', errMsg)
    store.commit('showLoader', false)
    // store.commit('setErrorMsg', "This is a 'dark' protein, i.e., it has no matching structure in Aquaria.")
  }
}

export function updateSequence (sequences) {
  store.commit('setCurrentSequence', sequences[0])
  textpanel.updateUniprotInfo(sequences[0])
}

// Initiated when new chain is selected
export async function chainSelected (primaryAccession, pdb, chain, attributes) {
  store.commit('setSecondaryStructure', null)
  var prevAccession = store.getters.getStructure.accession
  var prevPDB = store.getters.getStructure.pdb
  var prevChain = store.getters.getStructure.chain
  var chainData = store.state.pdbChainData
  pdb = pdb.includes('[') ? pdb.split('[')[0] : pdb
  var chainIndex = attributes.pdb_chain.indexOf(chain)
  store.commit('setGene', attributes.common_names[chainIndex])

  /** Update info panels if different chain selected */
  var newChain = isNewChain(primaryAccession, pdb, chain)
  if (newChain) {
    if (prevAccession && prevAccession !== primaryAccession && prevPDB === pdb && prevChain === chain) {
      /** If previously loaded a different sequence aligning to the same pdb */
      // notifyUser()
    }
    await updateStructure(primaryAccession, pdb, chain)
    resetPanel()
    // store.commit('setSecondaryStructure', newData.secondary_structure)
  } else if (chainData && prevAccession.length > 0) {
    var currentMember = store.state.Matches.clusters[store.state.pdbIndex.cluster].members[store.state.pdbIndex.member]
    textpanel.updatePDBChainInfo(pdb, chain, currentMember.alignment_identity_score, store.state.pdb_data.Organism[primaryAccession])
  }
}

async function updateStructure (primaryAccession, pdb, chain) {
  /** Store accession, pdb and chain of current view */
  store.commit('setStructure', { accession: primaryAccession, pdb: pdb, chain: chain[0] })
  store.commit('setCoveragemapOverlay', true)

  /** Fetch matching structures for sequence aligned to new chain */
  var loadRequest = {
    selector: [primaryAccession],
    selectPDB: pdb.includes('[') ? pdb.split('[')[0] : pdb, // optional fields
    selectChain: chain // optional fields
  }
  var matches = await getMatchingStructures(loadRequest)

  /** Check for variants and update best matching structure */
  if (Object.keys(store.getters.getVariantResidues).length < 0) {
    var data = await getTheBestStructureForVar(pdb, chain, primaryAccession, store.getters.getVariantResidues)
    matches.Selected_PDB = data.Selected_PDB ? data.Selected_PDB : data.Best_PDB
    if (data.variantStructs) {
      store.commit('setvariantStructs', data)
    }
  }

  /** Update PDB index */
  var pdbIndex = findPDBMember(pdb, chain[0])
  store.commit('setPdbIndex', pdbIndex)
}

function resetPanel () {
  /** Clear matching structures panel */
  document.querySelectorAll('#allclusters div').forEach(function (div) { div.remove() })
}

