/**
 * Initiate fetching of all features
 *
 * @param primary_accession
 *            object
 * @param featureCallback
 *            function
 */
export async function fetch_annotations (primary_accession) {
  totalServer = 0
  try {
    resetDASAnnotations()
    checkURLForFeatures(primary_accession).then(async function(){
      await processNextServer(primary_accession)
    })
  }
  catch (err) {
    console.log("Error encountered when loading added features")
    await processNextServer(primary_accession)
  }
};


/**
 * fetch data for each servers
 */
function getJsonFromUrl (requestedFeature, url, primary_accession) {
  const featuresFromExtServer = {}

  axios({
    method: 'get',
    url: url
  })
    .then(function (response) {
      if (requestedFeature == 'PredictProtein') {
        // convert feature first
        handlePredictProtein(response.data, primary_accession, variantResidues, requestedFeature)
        store.commit('setRmServerVal', requestedFeature)
      }
      if (requestedFeature == 'SNAP2') {
        handleSnap2(response.data, primary_accession, variantResidues, requestedFeature)
        store.commit('setRmServerVal', requestedFeature)
      }
      if (requestedFeature == 'CATH') {
        handleCathData(response.data, primary_accession, variantResidues)
        store.commit('setRmServerVal', requestedFeature)
      }
      if (requestedFeature == 'CathCovid') {
        handleCath_covid(response, primary_accession, variantResidues)
        store.commit('setRmServerVal', 'CATH')
      }

      if (requestedFeature == 'FunVar') {
        handleFunVar_cancer(response, primary_accession, variantResidues)
        store.commit('setRmServerVal', requestedFeature)
      }

      if (requestedFeature == 'FunVarCovid'){
        handleFunVar_covid(response, primary_accession, variantResidues)
        store.commit('setRmServerVal', 'FunVar')
      }

      if (requestedFeature == 'myVariant.info') {
        handleMyVariantInfo(response, primary_accession, variantResidues)
        store.commit('setRmServerVal', requestedFeature)
      }

      // return featuresFromExtServer
    })
    .catch(function (error) {

      if (requestedFeature == 'FunVarCovid'){
        store.commit('setRmServerVal', 'FunVar')
      }
      else if (requestedFeature == 'CathCovid') {
        store.commit('setRmServerVal', 'CATH')
      }
      else {
        store.commit('setRmServerVal', requestedFeature)
      }


      // handle error
      console.log('###################### Error ' + requestedFeature)
      console.log(error)

      finishServer(new Array(), primary_accession)

      // return featuresFromExtServer
    })

  return featuresFromExtServer
}

/**
 * Process the next annotation resource and add their annotations
 */
async function processNextServer (primary_accession) {
  currentServer += 1
  isFetchingData = true

  if (currentServer < servers.length) {
    // if (servers[currentServer].Server == 'Added Features') {
    //   processNextServer(primary_accession)
    // } else 
    if (servers[currentServer].Server == 'UniProt') {
      fetch_uniprot(primary_accession, servers[currentServer])
      await processNextServer(primary_accession)
    } else if (servers[currentServer].id == 'PredictProtein') {
      getJsonFromUrl(servers[currentServer].id, servers[currentServer].URL + primary_accession, primary_accession)
      await processNextServer(primary_accession)
    } else if (servers[currentServer].id == 'SNAP2') {
      getJsonFromUrl(servers[currentServer].id, servers[currentServer].URL + primary_accession + '&details', primary_accession)
      await processNextServer(primary_accession)
    } else if (servers[currentServer].id == 'CATH') {
      if (primary_accession == 'P0DTC1' || primary_accession == 'P0DTC2'|| primary_accession == 'P0DTC7' || primary_accession == 'P0DTD1'){
				// console.log("!!!!!!!!!! A COVID PROTEIN ENCOUNTERED");
				getJsonFromUrl('CathCovid',servers[currentServer]['URL_covid'] + primary_accession + '?content-type=application/json', primary_accession);
        await processNextServer(primary_accession)
	 		}
			else {
        getJsonFromUrl(servers[currentServer].id, servers[currentServer].URL + primary_accession + '?content-type=application/json', primary_accession)
        await processNextServer(primary_accession)
      }
    } else if (servers[currentServer].id == 'FunVar') {
      if (primary_accession == 'P0DTC1' || primary_accession == 'P0DTC2' || primary_accession == 'P0DTC7' || primary_accession == 'P0DTD1') {
        // FunVar covid
        getJsonFromUrl('FunVarCovid', servers[currentServer].URL_CATH_covid + primary_accession + '?content-type=application/json', primary_accession)
        await processNextServer(primary_accession)

	 		} else {
        // FunVar cancer
        getJsonFromUrl(servers[currentServer].id, servers[currentServer].URL_CATH_v2 + primary_accession + '?content-type=application/json', primary_accession)
        await processNextServer(primary_accession)
      }
    } else if (servers[currentServer].id == 'myVariant.info') {
      getJsonFromUrl(servers[currentServer].id, servers[currentServer].URL_myGene + primary_accession, primary_accession)
      await processNextServer(primary_accession)
    }
  } else {
    // console.log("fetch_features.processNextServer finish DAS");
    toDescAndAddToAdedFeat()
  }
}

