function createFeature (param) {
  var features = param.split('.')[0]
  let defaultColor =  '#F73C3C';
  var Features = []
  features.split(',').forEach(function (feature) {
    var resFeature = {}
    resFeature.Name = ''
    resFeature.Description = ''
    if (feature.includes('-')) {
      resFeature.Residues = feature
    } else {
      resFeature.Residue = parseInt(feature)
    }

    if (param.split('.').length === 4) {
      resFeature.Name = param.split('.')[1]
      resFeature.Description = param.split('.')[2]
    } else if (param.split('.').length === 3) {
      resFeature.Name = (param.split('.')[1].toLowerCase().includes('sidechain')) ? '' : param.split('.')[1]
      resFeature.Description = (param.split('.')[2].toLowerCase().includes('sidechain')) ? '' : param.split('.')[2]
    } else if (param.split('.').length === 2) {
      resFeature.Name = (param.split('.')[1].toLowerCase().includes('sidechain')) ? '' : param.split('.')[1]
    } else {
      var label = resFeature.Residue ? resFeature.Residue : resFeature.Residues
      resFeature.Name = param.toLowerCase().includes('sidechain') ? 'Residue ' + label  + ' - Show sidechain': 'Residue ' + label
      resFeature.Description = ''
    }
    resFeature.Name = resFeature.Name.replace('_', ' ')
    resFeature.Description = resFeature.Description.replace('_', ' ')

    if (param.includes('=')) {
      resFeature.Color = nameToHex(param.split('=')[1].toLowerCase())
    } else {
      resFeature.Color = defaultColor
    }
    Features.push(resFeature)
  })
  return (Features)
}

function addToVariantResidues(variantResidues, resNum, theOrigFeatStr, order, newAas, isOne, defaultDesc, consequent) {
  let incr = 0
  if (!variantResidues.hasOwnProperty(resNum)){
    variantResidues[resNum] = {}
  }

  if (!variantResidues[resNum].hasOwnProperty('theOrigFeatStr')){
    // Add
    variantResidues[resNum]['theOrigFeatStr'] = []
    variantResidues[resNum]['order'] = []
    variantResidues[resNum]['newAas'] = []
    variantResidues[resNum]['isOne'] = []
    variantResidues[resNum]['defaultDesc'] = []
    variantResidues[resNum]['consequent'] = []

  }

  if (!variantResidues[resNum]['theOrigFeatStr'].includes(theOrigFeatStr)){
    variantResidues[resNum]['theOrigFeatStr'].push(theOrigFeatStr)
    variantResidues[resNum]['order'].push(order)
    variantResidues[resNum]['newAas'].push(newAas)
    variantResidues[resNum]['isOne'].push(isOne)
    variantResidues[resNum]['defaultDesc'].push(defaultDesc)
    variantResidues[resNum]['consequent'].push(consequent)

    incr = 1
  }

  return incr
}

function addNewResAndGrantham (varinatResidues_resNum, consInfo_resNum) {
  if (consInfo_resNum.hasOwnProperty('oldRes')) {
    Object.values(threeToOneResMap).forEach(function (anAa, i) {
      const granthamStr = getGranthamIsCons(anAa, consInfo_resNum.oldRes)
      if (granthamStr != '') {
        if (!varinatResidues_resNum.hasOwnProperty(anAa)) {
          varinatResidues_resNum[anAa] = []
        }
        varinatResidues_resNum[anAa].forEach(function (index, i) {
          if(index.includes('Grantham:')) {
            varinatResidues_resNum[anAa].splice(i, 1)
          }
        })
        if (!varinatResidues_resNum[anAa].includes(granthamStr)) {
          varinatResidues_resNum[anAa].push(granthamStr)
        }
      }
    })
  }
}

