function nameToHex(name) {
  // Get RGB from named color in temporary div
  let fakeDiv = document.createElement("div");
  fakeDiv.style.color = name;
  document.body.appendChild(fakeDiv);

  let cs = window.getComputedStyle(fakeDiv),
      pv = cs.getPropertyValue("color");

  document.body.removeChild(fakeDiv);

  // Code ripped from RGBToHex() (except pv is substringed)
  let rgb = pv.substr(4).split(")")[0].split(","),
      r = (+rgb[0]).toString(16),
      g = (+rgb[1]).toString(16),
      b = (+rgb[2]).toString(16);

  if (r.length == 1)
    r = "0" + r;
  if (g.length == 1)
    g = "0" + g;
  if (b.length == 1)
    b = "0" + b;

  return "#" + r + g + b;
}

/**
 * Extracts user-defined variant, description and color.
 * @returns { featStr: featStr, description: desc, color: color}
 */
function extractVariantdesc (feature) {
  let desc = ''; let featStr = feature; let color = '';
  let regex = new RegExp(/[A-Za-z]+[0-9]+[A-Za-z\*\_\?\[\]\(\)\%\=\/]+/)

  let extData = feature.replace(/^p\./, "");
  // extData = extData.replace(/[\(\)]+/g, "");

  let featAndDesc = extData;

  if (extData.match(/[^\\]=[^\/]/)){
    let idx_1 = extData.lastIndexOf("\\=") 
    let idx = extData.lastIndexOf("=")

    if (idx != idx_1){
      console.log('The last value is ' + idx + ' ' + idx_1)

      featAndDesc = extData.slice(0, idx)
      color = extData.slice(idx + 1)

      if (color.match(/^[0-9A-Fa-f]{6}$/)){
        console.log("The color is a hex color " + color)
        color = '#' + color
      }
      // console.log("The color is " + color)
      let color_hex = nameToHex(color);

      // console.log("The color is " + color + ' ' + color_hex)
      color = color_hex;
      
      extData = featAndDesc
    }
  }
  if(extData.match(/[^\\]\./)) {
    let dataArray = extData.split(/[^\\]\./)
    if (dataArray.length > 1) {
      desc = dataArray[1].toLowerCase().includes('sidechain') ? '' : dataArray[1]
      desc = desc.replace(/\\/g, '')
      // console.log('The featStr here is (2) ' + desc)
    }
    featStr = regex.exec(featAndDesc)[0]
  }
  else {
    featStr = featAndDesc
  }

  console.log("The featStr here is " + featAndDesc + ' ' + desc + ' ' + color)
  return { featStr: featStr, description: desc, color: color}
}

function getTheTwoPositions (featStr) {

  const arr = featStr.split(/\_/)
  arr[0] = arr[0].replace(/[A-Za-z]/g, '')
  arr[1] = arr[1].replace(/[A-Za-z\*]/g, '')

  arr[0] = parseInt(arr[0])
  arr[1] = parseInt(arr[1])

  return { posStart: arr[0], posEnd: arr[1] }
}

