import { getConsInfo } from './consequentInfo'
import store from '../../../store/index'


export function getDataUrl(zenodoId){
  return new Promise(function(resolve1, reject1){
    var zenodoFilesData = {}
    let arrUrl_zenodo_fName = ''
    let arrUrl_zenodo_fileNum = -1
    let arrUrl_zenodo_fsName = ''
    let arrUrl_zenodo_fsNum = -1
    return new Promise(function(resolve2, reject2){
        


        let recordId = zenodoId.split('.')[1]; 
        // console.log("The zenodo URL is " + zenodoId + " " + recordId);

        
        
        let arrUrl_zenodo = zenodoId.split('.') 
        // console.log('The zenodo!! ' + arrUrl_zenodo.length)
        
        if (arrUrl_zenodo.length >= 3){
          if (isNaN(arrUrl_zenodo[2])){
            arrUrl_zenodo_fName = arrUrl_zenodo[2]
          }
          else {
            arrUrl_zenodo_fileNum = parseInt(arrUrl_zenodo[2])
          }
          // console.log('The Zenodo file name is ' + arrUrl_zenodo[2] + ' ' + arrUrl_zenodo_fName + ' ' + arrUrl_zenodo_fileNum)
        }

        if (arrUrl_zenodo.length >= 4){
          if (isNaN(arrUrl_zenodo[3])){
            arrUrl_zenodo_fsName = arrUrl_zenodo[3]
          }
          else {
            arrUrl_zenodo_fsNum = parseInt(arrUrl_zenodo[3])
          }
          // console.log('The zenodo feature set name is ' + arrUrl_zenodo[3])
        }
        // console.log('This arr is ' + arrUrl_zenodo)


        if (!recordId || !recordId.match(/^[0-9]+$/)){
            resolve2([], []);
        }
        
        let thePromises = []; let filenames = []
        let url = "https://zenodo.org/api/records/" + recordId

        // console.log(url)
        getFromLocation(url).then(function(data){
            // console.log("The data from zenodo is ")
            // console.log(data.data)
           if (data.hasOwnProperty('data') && data.data.hasOwnProperty('files') && data.data.files.length > 0){
              for (let i = 0; i < data.data.files.length; i++) {
                
                if (data.data.files[i].hasOwnProperty('links') && data.data.files[i].links.hasOwnProperty('download')){

                  if (arrUrl_zenodo_fName != ''){
                    // if (arrUrl_zenodo_fName == ) 
                    if (data.data.files[i].filename == arrUrl_zenodo_fName + '.json'){
                      thePromises.push(getFromLocation(data.data.files[i].links.download)) 
                      filenames.push(data.data.files[i].filename)
                    }
                  }
                  else if (arrUrl_zenodo_fileNum != -1){
                    if (arrUrl_zenodo_fileNum == (i + 1)){
                      // console.log("yay, this is cool and stuff " + arrUrl_zenodo_fName)
                      thePromises.push(getFromLocation(data.data.files[i].links.download)) 
                      filenames.push(data.data.files[i].filename)

                    }
                  }
                  else {
                    thePromises.push(getFromLocation(data.data.files[i].links.download)) 
                    filenames.push(data.data.files[i].filename)
                  }
                  

                }

                if (i == data.data.files.length - 1){
                  // console.log("The ZENODO filenames are: " + filenames)
                  resolve2({thePromises: thePromises, filenames: filenames})
                }
              }
            }
            else {
              var errMsg = "There is a problem with this Zenodo record. The record is empty, or does not contain any JSON files. You can fix this by editing the Zenodo record, checking and editing the record identifier specified in the URL, or pressing 'OK' to load without a Zenodo record. <br><br>Details on setting up a Zenodo record for use with Aquaria can be found in our <a href='https://doi.org/10.1101/2021.09.10.459756' target='_blank'> preprint</a>."
              store.commit('setErrorTitle', 'Zenodo record error')
              store.commit('setErrorMsg', errMsg)
              store.commit('setFallback', 1)
              store.commit('showLoader', false)

              throw('Zenodo record error')
              resolve2(thePromises, filenames)
            }
        })
    }).then (function(obj){
      return new Promise(function (resolve3, reject3){
        // console.log('zenodo filename ' + obj.filenames + ' ' + obj.thePromises.length)

        if (!obj.hasOwnProperty('thePromises') || obj.thePromises == null || obj.thePromises == undefined || obj.thePromises.length == 0){
          store.commit('setErrorTitle', 'JSON file not found in the Zenodo record')
          
          if (arrUrl_zenodo_fName !== '' ){
            store.commit('setErrorMsg', '\'' + arrUrl_zenodo_fName + '\'' + ' is not found in the specified Zenodo record. The filename specification is case-sensitive. You can fix this by directly editing the URL, or press \'OK\' to load all files in the Zenodo record.')
            store.commit('setFallback', 2)
          }
          else if (arrUrl_zenodo_fileNum !== -1){
            store.commit('setErrorMsg', 'File number ' + arrUrl_zenodo_fileNum + ' is not found in the Zenodo record. You can fix this by directly editing the URL, or press \'OK\' to load all files in the Zenodo record.') 
            store.commit('setFallback', 2)
          }
          else {
            store.commit('setErrorMsg', "No JSON file not found in the Zenodo record. ")
            store.commit('setFallback', 1)
          }
          store.commit('showLoader', false)
          // alert('File in zenodo: ' + obj.filenames[i] + ' is not valid JSON.')
          throw('JSON file not found in the Zenodo record')
          resolve3()
        }
        Promise.all(obj.thePromises).then(function(fileData){
          // rearrangeFilesIfReq(fileData, obj.filenames)
          let thePromises2 = []
          for (let i =0; i<fileData.length; i++){
            
            // console.log("The zenodo data2 is ")
            // console.log(fileData[i])
            
            if (obj.filenames[i].match(/\.json$/) && typeof (fileData[i].data) === 'string'){
              

              // var errMsg = "Zenodo file is not valid JSON"
              store.commit('setErrorTitle', 'Zenodo file is not valid JSON')
              store.commit('setErrorMsg', 'File in zenodo: ' + obj.filenames[i] + ' is not valid JSON. Click \'OK\' to continue loading other files in the Zenodo record, or \'Cancel\' to load without Zenodo.')
              store.commit('setFallback', 1)
              store.commit('showLoader', false)
              // alert('File in zenodo: ' + obj.filenames[i] + ' is not valid JSON.')
              throw('File in zenodo: ' + obj.filenames[i] + ' is not valid JSON.')
            }
            else if (typeof (fileData[i].data) === 'object'){
              thePromises2.push(addToTotalData(zenodoFilesData, fileData[i], obj.filenames[i], arrUrl_zenodo_fsName, arrUrl_zenodo_fsNum))
            }
            
            if (i == fileData.length - 1) {
              resolve3(thePromises2)
            }
          }
      })

        /* 
        console.log("Combined zenodo data is ")
        console.log(zenodoFilesData)
        resolve1({data: zenodoFilesData})
        */
      })
    })
    .then (function (){
      console.log("Combined zenodo data is ")
      console.log(zenodoFilesData)
      resolve1({data: zenodoFilesData})
    })
  })
}



function addToTotalData(allZenodoData, zenodoFileData, filename, arrUrl_zenodo_fsName, arrUrl_zenodo_fsNum){
  return new Promise (function (resolve, reject){
    if (zenodoFileData.hasOwnProperty('data')){
      propagateSeqHashIfPres(zenodoFileData).then(function(){
        let isFeatureSetNameFound = false
        let keys = Object.keys(zenodoFileData.data);
        // console.log('The zenodo keys are ' + keys) 
        for (let i = 0; i < keys.length; i++){
          if (keys[i] == 'About'){
            if (!allZenodoData.hasOwnProperty('About')){
              allZenodoData['About'] = {}
            }
            
            if (zenodoFileData.data[keys[i]].hasOwnProperty('Title')){
              if (!allZenodoData['About'].hasOwnProperty('Title')){
                allZenodoData['About']['Title'] = filename + ": " + zenodoFileData.data[keys[i]]['Title']
              }
              else {
                allZenodoData['About']['Title'] = allZenodoData['About']['Title'] + '<br>' +  filename + ": " + zenodoFileData.data[keys[i]]['Title']
              }
            }
            if (zenodoFileData.data[keys[i]].hasOwnProperty('Name')){
              if (!allZenodoData['About'].hasOwnProperty('Name')){
                allZenodoData['About']['Name'] = filename + ": " + zenodoFileData.data[keys[i]]['Name']
              }
              else {
                allZenodoData['About']['Name'] = allZenodoData['About']['Name'] + '<br>' +  filename + ": " + zenodoFileData.data[keys[i]]['Name']
              }
            }
             

            if (zenodoFileData.data[keys[i]].hasOwnProperty('Description')){
              if (!allZenodoData['About'].hasOwnProperty('Description')){
                allZenodoData['About']['Description'] = filename + ": " + zenodoFileData.data[keys[i]]['Description']
              }
              else {
                allZenodoData['About']['Description'] = allZenodoData['About']['Description'] + '<br>' + filename + ": " + zenodoFileData.data[keys[i]]['Description']
              }
            }

            if (zenodoFileData.data[keys[i]].hasOwnProperty('DOI') || zenodoFileData.data[keys[i]].hasOwnProperty('URL')){
              if (!allZenodoData['About'].hasOwnProperty('DOI') || !allZenodoData['About'].hasOwnProperty('URL')){

                if (zenodoFileData.data[keys[i]].hasOwnProperty('DOI')){
                  allZenodoData['About']['DOI'] = filename + ": " + zenodoFileData.data[keys[i]]['DOI']  
                }
                else {
                  allZenodoData['About']['URL'] = filename + ": " + zenodoFileData.data[keys[i]]['URL']
                }
              }
              else{

                if (zenodoFileData.data[keys[i]].hasOwnProperty('DOI')){
                  allZenodoData['About']['DOI'] = allZenodoData['About']['DOI']  + '<br>' + filename + ": " + zenodoFileData.data[keys[i]]['DOI']
                }
                else {
                  allZenodoData['About']['URL'] = allZenodoData['About']['URL']  + '<br>' + filename + ": " + zenodoFileData.data[keys[i]]['URL']
                }
              }
            }

            if (zenodoFileData.data[keys[i]].hasOwnProperty('Source')){
              if (!allZenodoData['About'].hasOwnProperty('Source')){
                allZenodoData['About']['Source'] = filename + ": " + zenodoFileData.data[keys[i]]['Source']
              }
              else{
                allZenodoData['About']['Source'] = allZenodoData['About']['Source'] + '<br>' + filename + ": " + zenodoFileData.data[keys[i]]['Source']
              }
            }
            
          }
          else if (keys[i] == 'Sequence_hash'){
            /* if (!allZenodoData.hasOwnProperty('About')){
              allZenodoData[keys[i]] = JSON.parse(JSON.stringify(zenodoFileData.data[keys[i]]))
            } */ 
          }
          else {
            if (arrUrl_zenodo_fsName != ''){
              if (arrUrl_zenodo_fsName == keys[i]){
                var filename_v1 = filename.replace('.json', '') 
                allZenodoData[filename_v1 + '.' + keys[i]] = JSON.parse(JSON.stringify(zenodoFileData.data[keys[i]]))
                isFeatureSetNameFound = true
              }
            }
            else if (arrUrl_zenodo_fsNum != -1){
              if (parseInt(arrUrl_zenodo_fsNum) == (i+1)){
                // console.log("Zenodo comes here! " + arrUrl_zenodo_fsNum + ' ' + (i+1))
                var filename_v1 = filename.replace('.json', '') 
                allZenodoData[filename_v1 + '.' + keys[i]] = JSON.parse(JSON.stringify(zenodoFileData.data[keys[i]]))

                isFeatureSetNameFound = true
              }
            }
            else{
              var filename_v1 = filename.replace('.json', '') 
              allZenodoData[filename_v1 + '.' + keys[i]] = JSON.parse(JSON.stringify(zenodoFileData.data[keys[i]]))
            }
          }
  
          if (i == keys.length -1 ){
            console.log("Attention crew, this is your captain:")
            console.log(allZenodoData)

            if ((arrUrl_zenodo_fsName != '' || arrUrl_zenodo_fsNum != -1)){
              if (isFeatureSetNameFound == false){


                store.commit('setErrorTitle', 'Featureset not found')
                if (arrUrl_zenodo_fsName != ''){
                  store.commit('setErrorMsg', 'The featureset \'' + arrUrl_zenodo_fsName + '\' specified in the URL has not been identified in the specified feature file. The featuresets identifiers are case-sensitive. You can fix this by directly editing the URL, or press \'OK\' to load all features.')
                }
                else if (arrUrl_zenodo_fsNum != -1){
                  store.commit('setErrorMsg', 'The featureset \'' + arrUrl_zenodo_fsNum + '\' specified in the URL has not been identified in the specified feature file. You can fix this by directly editing the URL, or press \'OK\' to load all features.')
                } 
                store.commit('setFallback', 3)
                store.commit('showLoader', false)
                // store.commit('setErrorShowReportIssue', false)
                // alert('File in zenodo: ' + obj.filenames[i] + ' is not valid JSON.')
                throw('Featureset not found.')
              }
              else {
                resolve()
              }
            }
            else{
              resolve()
            }
          }
        }
      })
      
      
    }
    else {
      var errMsg = "Error with Zenodo filename. No files found."
      store.commit('setErrorTitle', 'Error with Zenodo filename')
      store.commit('setErrorMsg', errMsg)
      store.commit('setFallback', 3)
      store.commit('showLoader', false)
      throw('Error with Zenodo filename.')
      resolve()
    }
  })
}

var axios = require('axios')
// const { resolve } = require('core-js/fn/promise')
function getFromLocation (url) {
  return new Promise(function (resolve, reject) {
    const featuresFromExtServer = {}

    axios({
      method: 'get',
      url: url
    })
      .then(function (response) {
        // handle success
        // console.log("Success");
        // console.log(response.data);
        featuresFromExtServer.status = 'success'
        featuresFromExtServer.data = response.data
        resolve(featuresFromExtServer)
      })
      .catch(function (error) {
        // handle error
        console.log('Error')
        console.log(error)

        featuresFromExtServer.status = 'error'
        resolve(featuresFromExtServer)
      })
  })
}

function propagateSeqHashIfPres(addedFeatData){
  return new Promise(function(resolve, reject){
    let seqHash = '';
    if (addedFeatData.hasOwnProperty('About') && addedFeatData.About.hasOwnProperty('Sequence_hash')) {
      seqHash = addedFeatData.About.Sequence_hash; 
    }
    else {
      resolve()
    }

    let keys = Object.keys(addedFeatData); 

    if (keys.length == 0){
      resolve()
    }

    for (let i = 0; i < keys.length; i++){
      if (addedFeatData[keys[i]] !== 'About'){
        if (!addedFeatData[keys[i]].hasOwnProperty('Sequence_hash')){
          addedFeatData[keys[i]]['Sequence_hash'] = seqHash; 
        }
      }

      if (i == keys.length - 1){
        resolve()
      }
    }

    // console.log("Object keys length " + Object.keys(addedFeatData).length);
  })
}

function addColorIfMissing(aFeatSet){
  //console.log("A featset is ")
  //console.log(aFeatSet)

  let defaultColor =  '#F73C3C';
  if (!aFeatSet.hasOwnProperty('Color')){
      aFeatSet['Color'] = defaultColor;
  }
  else if (aFeatSet.hasOwnProperty('Color') && !aFeatSet['Color'].match(/^#/)) {
    aFeatSet['Color'] = colorToHex(aFeatSet['Color']);
  }
}

function colorToRGBA(color) {
  // Returns the color as an array of [r, g, b, a] -- all range from 0 - 255
  // color must be a valid canvas fillStyle. This will cover most anything
  // you'd want to use.
  // Examples:
  // colorToRGBA('red')  # [255, 0, 0, 255]
  // colorToRGBA('#f00') # [255, 0, 0, 255]
  var cvs, ctx;
  cvs = document.createElement('canvas');
  cvs.height = 1;
  cvs.width = 1;
  ctx = cvs.getContext('2d');
  ctx.fillStyle = color;
  ctx.fillRect(0, 0, 1, 1);
  return ctx.getImageData(0, 0, 1, 1).data;
}

function byteToHex(num) {
  // Turns a number (0-255) into a 2-character hex number (00-ff)
  return ('0'+num.toString(16)).slice(-2);
}

function colorToHex(color) {
  // Convert any CSS color to a hex representation
  // Examples:
  // colorToHex('red')            # '#ff0000'
  // colorToHex('rgb(255, 0, 0)') # '#ff0000'
  var rgba, hex;
  rgba = colorToRGBA(color);
  hex = [0,1,2].map(
      function(idx) { return byteToHex(rgba[idx]); }
      ).join('');
  return "#"+hex;
}

export function extractVariantResidues(zenodoFileData, variantResidues, checkAndPushToArr, addNewResAndGrantham, addThinSpaces, selOldAaBasedOnIsOne, Store, addToVariantResidues){
  return new Promise(function(resolve, reject){

    // let variantResidues = {}; 
    let posCounter =0;
    
    let featSets = Object.keys(zenodoFileData)
    if (featSets.length == 0){
      resolve(variantResidues)
    }
    for (let i=0; i < featSets.length; i ++){
      // console.log("HELLO WORLD! ")
      // console.log(zenodoFileData[featSets[i]])
      if (zenodoFileData[featSets[i]].hasOwnProperty('Features')){
        // Loop through each feature
        for (let j=0; j < zenodoFileData[featSets[i]]['Features'].length; j++){
          let featName = zenodoFileData[featSets[i]]['Features'][j].Name
          let defaultDesc = '';
          if (zenodoFileData[featSets[i]]['Features'][j].hasOwnProperty('Description') && zenodoFileData[featSets[i]]['Features'][j]){
            defaultDesc = zenodoFileData[featSets[i]]['Features'][j].Description
          }
          var consInfoRes = {}; let isToAdd = true
          try {
            consInfoRes = getConsInfo(featName)
            // console.log('ConsInfo extracted as: ') 
            // console.log(consInfoRes)
            // console.log(zenodoFileData[featSets[i]]['Features'][j])
          }
          catch (err) {
            console.log("No variant here " + err)
            isToAdd = false 
          }

            // featAndDesc.featStr = consInfoRes.theOrigFeatStr;
          if (isToAdd == true && consInfoRes.hasOwnProperty('newResidues') && consInfoRes.hasOwnProperty('theOrigFeatStr') && Object.keys(consInfoRes.newResidues).length > 0){



            addColorIfMissing(zenodoFileData[featSets[i]]['Features'][j])

            let consInfo = consInfoRes.newResidues;
            // console.log('Feature name ' + featName + ' ' + consInfoRes.theOrigFeatStr)
            let resNums_ = Object.keys(consInfo)
            //console.log('What if he knows now ... ' + resNums_)
            
            let addTheResProp = false
            if (!zenodoFileData[featSets[i]]['Features'][j].hasOwnProperty('Residue') && !zenodoFileData[featSets[i]]['Features'][j].hasOwnProperty('Residues')) {
              if (resNums_.length == 1 && resNums_[0].match(/[^\-]/)) {
                zenodoFileData[featSets[i]]['Features'][j].Residue = parseInt(resNums_[0])
              }
              else {
                zenodoFileData[featSets[i]]['Features'][j].Residues = []
              }
              addTheResProp = true
              // console.log("here!! addTheResProp")
            }
            
            
            for (const resNum in consInfo) {
              if (addTheResProp == true && Object(zenodoFileData[featSets[i]]['Features'][j]).hasOwnProperty('Residues')) {
                zenodoFileData[featSets[i]]['Features'][j].Residues.push(parseInt(resNum))
              }
              if (consInfo[resNum].hasOwnProperty('consStr')){
                // console.log("We do not want unknown consequence " + consInfo[resNum].consStr)
              }

              let theOrigFeatStr = addThinSpaces(consInfoRes.theOrigFeatStr, consInfo[resNum].consStr)
              let newAas = []; let isOne = true; let consequent = ''; 
              if (consInfo[resNum].hasOwnProperty('newAas')) {
                newAas = consInfo[resNum].newAas.slice()
              }
              if (consInfoRes.hasOwnProperty('isOne')) {
                isOne = consInfoRes.isOne
              }
              /*
              if (consInfoRes.hasOwnProperty('defaultDesc')) {
                defaultDesc = aFeature.Description
              }
              */
              if (consInfo[resNum].hasOwnProperty('consStr')){
                consequent = consInfo[resNum].consStr
              }
      
              let theIncr = addToVariantResidues(variantResidues, resNum, theOrigFeatStr, posCounter, newAas, isOne, defaultDesc, consequent); 
              posCounter = posCounter + theIncr;

              
              if (consInfo[resNum].hasOwnProperty('oldRes')) {
                variantResidues[resNum].oldAa = consInfo[resNum].oldRes
                console.log("ConsInof has old AA " + consInfo[resNum].oldRes + ' ' + resNum)
                console.log(consInfo)
              }
              else {
                console.log("ConsInof does not have old AA ")
                
              }
              addNewResAndGrantham(variantResidues[resNum], consInfo[resNum])
           
              // Renaming for identification in feature set.   
              if (consInfo[resNum].hasOwnProperty('oldRes') && consInfoRes.hasOwnProperty('isOne') && consInfo[resNum].hasOwnProperty('newAas')){


                let featName = theOrigFeatStr // zenodoFileData[featSets[i]]['Features'][j].Name 
                zenodoFileData[featSets[i]]['Features'][j].Name = "<b><span id='urlVariant' class='urlVariant'>" + featName + "</span></b><span id='span_missenseHeading' class='pAaColor'> (<span id='oldAa'>" + selOldAaBasedOnIsOne(consInfo[resNum].oldRes, consInfoRes.isOne)  + '</span> &#8201;&#8594;&#8201; <span id="newAa">'
                if (consInfo[resNum].newAas[0] != 'X'){
                  zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + selOldAaBasedOnIsOne(consInfo[resNum].newAas[0], consInfoRes.isOne)
                }
                zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + '</span>)</span>'
              } 
              else {
                zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + " <span id='span_missenseHeading' class='pAaColor'> </span> "
              }
              zenodoFileData[featSets[i]]['Features'][j].Name = zenodoFileData[featSets[i]]['Features'][j].Name + " <span id='consInfo' class='pAaColor'> " + consInfo[resNum].consStr + '</span>'
             

              // variantResidues[resNum].defaultDesc = aFeature.Description
             

            }
          }
          /* }
          catch (err) {
            console.log('Catching err in handleZenodo: ' + err)
            
          }  */
          




    
        }
      }
      
      if (i == featSets.length - 1){ 
        // Check structure counts
        // let pdb = ''; let chain = '';



        // console.log("The protein primary accession is " + window.AQUARIA.protein_primary_accession)
        /* 
        loadData.getTheBestStructureForVar(pdb, chain, store.state.structure.accession, variantResidues).then(function(varStructs){
        // loadData.getTheBestStructureForVar2('7lab', 'A', 'P0DTC2', variantResidues).then(function(varStructs){
          // console.log('THE VAR STRUCTS ARE ')
          if (varStructs && varStructs.hasOwnProperty('variantStructs')){
            console.log('THE VAR STRUCTS ARE ')
            console.log(varStructs)
            Store.commit('setvariantStructs', varStructs)
          }
        })
        */
        // console.log()
        resolve(variantResidues)
      }
    }
  })

}
