import axios from 'axios'
import pako from './pako'
import store from '../../../store/index'
import * as TopTenStore from '../../../utils/topten'

export default class DataServer {
  constructor (url, id) {
    this.url = url
    this.pdbId = id
  }

  getProteinData (callback) {
    var pdbIndex = store.state.pdbIndex
    // fetch(this.url).then(res => res.text()).then(pdbText => callback({ pdbId: this.pdbId, pdbText }));
    axios({
      method: 'get',
      url: this.url,
      responseType: 'arraybuffer',
      'accept-encoding': 'gzip'
		  }).then(function (response) {
      store.commit('showLoader', true)
      // const pdbText = response.data
      const pdbText = pako.inflate(response.data, { to: 'string' })
      callback({ pdbId: store.state.structure.pdb, pdbText })
      if (localStorage.getItem('LastSuccess') === null) {
        store.commit('setHelpTitle', 'Welcome to Aquaria')
      } else {
        store.commit('setNewUserStatus', false)
      }
      // var pdbTopTen = TopTenStore.TopTen('pdbTopten', 10)
      localStorage.setItem('LastSuccess', window.location.pathname)
      if (!TopTenStore.previousLookupByName(store.getters.getOrganism + '_' + store.getters.getGene)) {
        TopTenStore.submitFired(store.getters.getOrganism + '_' + store.getters.getGene, store.getters.getStructure.accession, store.state.structure.pdb)
      } else {
        TopTenStore.updatePDB(store.getters.getStructure.accession, store.state.structure.pdb)
      }
    }).catch(function (err) {
      console.log('Error fetching structure', err)
      if (store.state.UserInput && err.message.includes('Network') && !store.state.nonUserInput) {
        // Can't load PDB file
        store.commit('setNonUserInput', false)
        showError('PDB not available', '')
      }
      else if (store.state.Matches.clusters[store.state.pdbIndex.cluster].members.length - 1 <= store.state.pdbIndex.member) {
        // Load next structure
        if (store.state.Matches.clusters.length < store.state.pdbIndex.cluster && store.state.iteration < 1) {
          store.commit('setIteration', store.state.iteration + 1)
          pdbIndex.cluster = store.state.pdbIndex.cluster + 1
          pdbIndex.member = 0
          store.commit('setPdbIndex', pdbIndex)
          var selectedMember = store.state.Matches.clusters[pdbIndex.cluster].members[pdbIndex.member]
          store.commit('showLoader', true)
          store.commit('setLoaderMsg', 'Waiting for data...')
          window.location.hash = 'try'
          window.location.pathname = store.getters.getStructure.accession + '/' + selectedMember.pdb_id
        } else {
          showError('PDB not available', '')
        }
      } 
      else {
        if (store.state.iteration < 1) {
          store.commit('setIteration', store.state.iteration + 1)
          pdbIndex.member = store.state.pdbIndex.member + 1
          store.commit('setPdbIndex', pdbIndex)
          var selectedMember = store.state.Matches.clusters[pdbIndex.cluster].members[pdbIndex.member]
          store.commit('showLoader', true)
          store.commit('setLoaderMsg', 'Waiting for data...')
          window.location.hash = 'try'
          window.location.pathname = store.getters.getStructure.accession + '/' + selectedMember.pdb_id
        } else {
          showError('Problem loading matching structures', "Press 'OK' to keep trying, or 'Cancel' return to previous structure.")
        }
      }
    })
  }

  getViews (callback) {
    callback([])
  }

  saveViews (views, callback) {
    callback()
  }

  deleteView (viewId, callback) {
    callback()
  }
}

function showError (title, msg) {
  store.commit('setErrorTitle', title)
  store.commit('setErrorMsg', msg + "<br><br> You can view this structure on the PDB website. \n Press 'Report Issue' to tell the Aquaria team, or press 'OK' to return to previous structure.")
  store.commit('showLoader', false)
  document.querySelector('#Structures > a > span').click()
  console.log('PDB not found')
}

export { DataServer }
