import LRU from 'lru-cache'
var id, cache

export function TopTen (structureId, MAX_SIZE) {
  id = structureId
  var existing = localStorage.getItem(structureId)
  cache = new LRU(MAX_SIZE)
  if (existing === null) {
  } else {
    var entries = JSON.parse(existing)
    entries.forEach(function (entry) {
      cache.set(entry.key, entry.value)
    })
  }
}

export function submitFired (name, primaryAccession, pdb) {
  var last = { name: name, primary_accession: primaryAccession, pdb_id: pdb }
  cache.set(primaryAccession + pdb, last)
  save()
}

export function save () {
  var saveArray = []
  cache.forEach(function (value, key) {
    saveArray.push({ key: key, value: value })
  })
  var savedText = JSON.stringify(saveArray)
  console.log('TopTen.save', saveArray)
  localStorage.setItem(id, savedText)
}

export function getAll () {
  var ret = []
  cache.forEach(function (value) {
    ret.push(value)
  })
  return ret
}

export function savePDB (primaryAccession, pdb) {
  var store = {}
  store.primary_acccession = primaryAccession
  store.pdb = pdb
  cache.push(store)
}

export function previousLookup (primaryAccession) {
  var pdb = false
  cache.forEach(function (value) {
    if (value.primary_accession === primaryAccession) {
      pdb = value.pdb_id
    }
  })
  return pdb
}

export function previousLookupByName (name) {
  var pdb = false
  cache.forEach(function (value) {
    if (value.name === name) {
      pdb = value.pdb_id
    }
  })
  return pdb
}

export function updatePDB (primaryAccession, pdb) {
  cache.forEach(function (value) {
    if (value.primary_accession === primaryAccession) {
      value.pdb_id = pdb
    }
  })
  save()
}
